"use client";

import React, { useState } from 'react';
import HeaderComp from "@/components/Header";
import AppSidebar from "@/components/AppSidebar";

export default function HomePage() {
  const [isMobileMenuOpen, setMobileMenuOpen] = useState(false);
  // ۱. وضعیت باز و بسته بودن سایدبار دسکتاپ را هم مدیریت می‌کنیم
  const [isDesktopExpanded, setDesktopExpanded] = useState(true); // یا false به عنوان پیش‌فرض

  // ۲. عرض سایدبار را بر اساس وضعیت آن محاسبه می‌کنیم
  const sidebarWidth = isDesktopExpanded ? '16rem' : '5rem'; // مقادیر باید با SCSS یکی باشد

  return (
    <div className="relative min-h-screen bg-gray-100 dark:bg-slate-900">
      
      {/* سایدبار position: fixed است و از جریان خارج است */}
      <AppSidebar
        isSidebarOpenForMobile={isMobileMenuOpen}
        onCloseMobile={() => setMobileMenuOpen(false)}
        // یک پراپ جدید برای کنترل باز و بسته بودن از بیرون
        // شما باید این را در AppSidebar و سپس Sidebar پیاده‌سازی کنید
        isExpanded={isDesktopExpanded}
        // ...
      />

      {/* ۳. یک کانتینر برای محتوای اصلی که به اندازه سایدبار padding می‌گیرد */}
      <div 
        className="transition-all duration-300 ease-in-out"
        style={{ paddingRight: sidebarWidth }} // در حالت RTL
        // style={{ paddingLeft: sidebarWidth }} // در حالت LTR
      >
        <HeaderComp 
            onMenuButtonClick={() => setMobileMenuOpen(true)}
            // ...
        />
        
        <main className="p-8">
            <h1 className="text-2xl font-semibold">صفحه اصلی</h1>
            <p className="mt-2">به پنل مدیریت خوش آمدید.</p>
        </main>
      </div>
    </div>
  );
}